.page__content {
  padding-top: 40px;
  padding-bottom: 66px;
  display: grid;
  row-gap: 50px;
}

@media screen and (max-width: 896px) {
  .page__content {
    max-width: calc(100% - 19px * 2);
    margin: 0 auto;
    padding-top: 42px;
    padding-bottom: 48px;
    row-gap: 36px;
  }
}
